.modal {
  position: fixed;
  padding: 20px;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 10;
  min-height: 100%;
  overflow-y: auto;
  overscroll-behavior: contain;
}
.banner {
  text-align: center;
  margin-bottom: 20px;
}
.banner img {
  max-width: 196px;
}

.close {
  border-radius: 2px;
  padding: 10px;
  margin: 10px auto;
  cursor: pointer;
  width: fit-content;
  margin-bottom: 3rem;
}

.content {
  max-width: 540px;
  margin: auto;
}

@mixin lightTheme {
  .modal {
    background-color: #fffe;
    color: black;
  }

  .close {
    background-color: black;
    border: 1px solid black;
    color: white;
  }
}

@mixin darkTheme {
  .modal {
    background-color: #000e;
    color: white;
  }

  .close {
    background-color: white;
    border: 1px solid white;
    color: black;
  }
}

@media (prefers-color-scheme: light) {
  @include lightTheme
}

@media (prefers-color-scheme: dark) {
  @include darkTheme
}

.dark {
  @include darkTheme
}

.light {
  @include lightTheme
}