.modal {
  position: fixed;
  padding: 20px;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 10;
  min-height: 100%;
  overflow-y: auto;
  overscroll-behavior: contain;
}

.close {
  border-radius: 2px;
  padding: 10px;
  color: black;
  margin: 10px auto;
  cursor: pointer;
  width: fit-content;
  margin-top: 3rem;
  margin-bottom: 3rem;
}

h2 {
  text-align: center;
}

.content {
  max-width: 540px;
  margin: auto;
}

.result-table {
  width: 100%;
  max-width: 240px;
  margin: auto;
  font-size: 18px;
  border-collapse: collapse;

  tr {
    border-bottom-width: 1px;
    border-bottom-style: solid;
    line-height: 2rem;

    &:last-child {
      border-bottom: none;
    }
  }
}

@mixin lightTheme {
  .modal {
    background-color: #fffe;
    color: black;
  }

  .close {
    background-color: black;
    border: 1px solid black;
    color: white;
  }

  .result-table {
    tr {
      border-bottom-color: #d3d6da;
      ;
    }
  }
}

@mixin darkTheme {
  .modal {
    background-color: #000e;
    color: white;
  }

  .close {
    background-color: white;
    border: 1px solid white;
  }

  .result-table {
    tr {
      border-bottom-color: #3a3a3c;
      ;
    }
  }
}


@media (prefers-color-scheme: light) {
  @include lightTheme
}

@media (prefers-color-scheme: dark) {
  @include darkTheme
}

.dark {
  @include darkTheme
}

.light {
  @include lightTheme
}