






$light-gray: #a2a2a2;
$dark-gray: #818384;

html {
  margin-top: 0;
}

.App {
  max-width: 540px;
  margin: auto;
  text-align: left;
  padding-top: 0;
}

body.scroll-lock {
  position:fixed;
  overflow:hidden;
}

header {
  padding: 5px 5px;
  margin-bottom: 2rem;
  table {
    width: 100%;
  }
  .title-wrapper {
    text-align: center;
  }
  h1.title {
    font-weight: normal;
    font-size: 26px;
    text-align: center;
    pointer-events: none;
    margin: 0;
    font-weight: bold;
  }
  @media screen and (max-width: 540px) {
    h1.title {
      line-height: 2rem;;
    }
  }
}

.input-cells {
  margin: auto;
  .cell {
    text-transform: uppercase;
    text-align: center;
    &[data-animation='pop'] {
      animation-name: PopIn;
      animation-duration: 100ms;
    }
    &[data-animation='flip-in'] {
      animation-name: FlipIn;
      animation-duration: 125ms;
      animation-timing-function: ease-in;
    }
    &[data-animation='flip-out'] {
      animation-name: FlipOut;
      animation-duration: 125ms;
      animation-timing-function: ease-in;
    }
  }
}

.icon {
  cursor: pointer;
}

.help {
  border: 2px solid gray;
  padding: 2px 7px;
  border-radius: 100%;
}

.keys {
  color: black;
  margin-top: 2rem;
}

.message {
  text-align: center;
  padding: 20px;
  margin: 1rem;
  &[data-animation='fade-in'] {
    animation-name: FadeIn;
    animation-duration: 0.5s;
    animation-timing-function: ease-in;
  }
}
.message-success {
  background-color: #d1e7dd;
  border-color: #badbcc;
  color: #0f5132;
}
.message-failure {
  background-color: #f8d7da;
  border-color: #f5c2c7;
  color: #842029;
}

.guess {
  text-align: right;
  font-size: 8px;
}

.btn {
  display: inline-block;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  background-color: transparent;
  border: 1px solid transparent;
  border-top-color: transparent;
  border-right-color: transparent;
  border-bottom-color: transparent;
  border-left-color: transparent;
  padding: .375rem .75rem;
  font-size: 1rem;
  border-radius: .25rem;
  transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
  text-transform: uppercase;
}

.btn-primary {
  color: #fff;
  background-color: #0d6efd;
  border-color: #0d6efd;
}

.btn-pinky {
  color: #000;
  background-color: #ffc107;
  border-color: #ffc107;
}

.material-icons {
  vertical-align: middle;
  line-height: 0 !important;
  position: relative;
  top: -1px;
}
.hg-theme-default {
  .hg-button {
    border-radius: 0 !important;
  }
}

@keyframes PopIn {
  from {
    transform: scale(0.8);
    opacity: 0;
  }

  40% {
    transform: scale(1.1);
    opacity: 1;
  }
}

@keyframes FlipOut {
  0% {
    transform: rotateX(0);
  }
  100% {
    transform: rotateX(-90deg);
  }
}

@keyframes FlipIn {
  0% {
    transform: rotateX(-90deg);
  }
  100% {
    transform: rotateX(0);
  }
}

@keyframes FadeIn {
  0% {
    transform: scale(0);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

@mixin lightTheme {
  .App {
    color: black;
  }
  body {
    background-color: #fff;
  }
  header {
    background-color: #f8f9fa;
    border-bottom: 1px solid #d3d6da;
  }
  h1.title {
    color: #121213;
  }
  .input-cells .cell {
    background: white;
    color: #121213;
    border: 2px solid #d3d6da;
    &[data-state='tbd'] {
      border: 2px solid #878a8c
    }
  }
  .input-cells .cell.miss {
    background: #787c7e;
    border: 2px solid #787c7e;
    color: white;
  }
  .input-cells .cell.correct {
    background: #6aaa64;
    border: 2px solid #6aaa64;
    color: white;
  }
  .input-cells .cell.partial {
    background: #c9b458;
    border: 2px solid #c9b458;
    color: white;
  }
  .hg-theme-default.hg-layout-default {
    background-color: #fff;
    .hg-button {
      background-color: #d3d6da;
      border: none;
      color: black;
    }
    .miss {
      background: #787c7e;
    }
    .correct {
      background: #6aaa64;
    }
    .partial {
      background: #c9b458;
    }
  }
  .guess {
    color: white;
  }
  path {
    fill: black;
  }
  svg {
    fill: white;
  }
}

@mixin darkTheme {
  .App {
    color: white;
  }
  header {
    border-bottom: 1px solid #3a3a3c;
    background-color: #212529;
  }
  body {
    background-color: #121213;
  }
  h1.title {
    color: white;
  }
  .input-cells .cell {
    background: #121213;
    color: white;
    border: 2px solid #3a3a3c;
    &[data-state='tbd'] {
      border: 2px solid #565758;
    }
  }
  .input-cells .cell.miss {
    background: #3a3a3c;
    border: 2px solid #3a3a3c;
  }
  .input-cells .cell.correct {
    background: #57ac57;
    border: 2px solid #57ac57;
  }
  .input-cells .cell.partial {
    background: #b59f3b;
    border: 2px solid #b59f3b;
  }
  .hg-theme-default.hg-layout-default {
    background-color: black;
    .hg-button {
      background-color: #818384;
      border: none;
      color: white;
    }
    .miss {
      background: #3a3a3c;
    }
    .correct {
      background: #538d4e;
    }
    .partial {
      background: #b59f3b;
    }
  }
  .guess {
    color: black;
  }
  path {
    fill: white;
  }
}

@media (prefers-color-scheme: light) {
  @include lightTheme
}

@media (prefers-color-scheme: dark) {
  @include darkTheme
}

.dark {
  @include darkTheme
}

.light {
  @include lightTheme
}